	//保存进场人员
	@action
	savePerson() {
		return requestPost('savePerson', Config.xmgl.savePerson, Schemas.xmgl.savePerson, {
			p: Object.assign({}, this.personFields, {
				projectId: this.projectId
			})
		})
	}

	//保存质量信息
	@action
	saveQuality() {
		return requestPost('saveQuality', Config.xmgl.saveQuality, Schemas.xmgl.saveQuality, {
			q: Object.assign({}, {
				id: this.qualityFields.id,
				question: this.qualityFields.question,
				type: this.qualityType,
				emergency: this.qualityType == 1 ? this.convertEmergencyQuality2Int(this.qualityFields.emergencyQuality) : this.convertEmergencySafe2Int(this.qualityFields.emergencySafe),
				createTime: this.qualityFields.createTime,
				state: this.convertState2Int(this.qualityFields.state),
				solveTime: this.qualityFields.solveTime,
				handler: this.qualityFields.handler,
				reviewer: this.qualityFields.reviewer,
				remark: this.qualityFields.remark,
				projectId: this.projectId,
				updateTime: this.qualityFields.updateTime,
				planSolveTime: this.qualityFields.planSolveTime
			})
		})
	}

	// 删除质量、安全
	@action delQuality(param) {
		return requestPost('delQuality', Config.xmgl.delQuality, Schemas.xmgl.delQuality, {
			qualityId: param
		});
	}

	convertType2Int(type) {
		if (type === "质量") return 1
		if (type === "安全") return 2
	}

	convertType2String(type) {
		if (type === 1) return "质量"
		if (type === 2) return "安全"
	}

	convertEmergencyQuality2Int(type) {
		if (type === "质量通病") return 1
		if (type === "质量问题") return 2
		if (type === "一般质量问题") return 3
		if (type === "较大质量问题") return 4
		if (type === "重大质量问题") return 5
	}

	convertEmergencyQuality2String(type) {
		if (type === 1) return "质量通病"
		if (type === 2) return "质量问题"
		if (type === 3) return "一般质量问题"
		if (type === 4) return "较大质量问题"
		if (type === 5) return "重大质量问题"
	}

	convertEmergencySafe2Int(type) {
		if (type === "安全隐患") return 1
		if (type === "一般安全事故") return 2
		if (type === "较大安全事故") return 3
	}

	convertEmergencySafe2String(type) {
		if (type === 1) return "安全隐患"
		if (type === 2) return "一般安全事故"
		if (type === 3) return "较大安全事故"
	}

	convertState2Int(type) {
		if (type === "发现问题") return 1
		if (type === "开始整改") return 2
		if (type === "整改完成") return 3
		if (type === "验收完成") return 4
		if (type === "问题复现") return 5
	}

	convertState2String(type) {
		if (type === 1) return "发现问题"
		if (type === 2) return "开始整改"
		if (type === 3) return "整改完成"
		if (type === 4) return "验收完成"
		if (type === 5) return "问题复现"
	}


	// 进场人员查询
	@action
	getAllPersonByProjectIdAndType() {
		return requestPost('getAllPersonByProjectIdAndType', Config.xmgl.getAllPersonByProjectIdAndType, Schemas.xmgl.getAllPersonByProjectIdAndType, {
			projectId: this.projectId,
			type: this.personType,
			pageIndex: this.personPageIndex,
			pageSize: this.personPageSize
		}).then((res) => {
			this.personPageIndex = res.pageIndex;
			this.personPageSize = res.pageSize;
			this.personTotalCount = res.totalCount;
			this.personTableData = Object.assign([], res.body.map((item, index) => {
				Object.assign(item, {key: index + 1});
				if (index == 0) {
					return Object.assign(item, {selested: true})
				} else {
					return Object.assign(item, {selested: false})
				}
			}));
			return res;
		})
	}

	// 进场人员工种查询
	@action
	getPersonType() {
		return requestPost('getAllPersonByProjectIdAndType', Config.xmgl.getAllPersonByProjectIdAndType, Schemas.xmgl.getAllPersonByProjectIdAndType, {
			projectId: this.projectId,
			type: this.personType,
			pageIndex: 0,
			pageSize: 100
		}).then((res) => {
			let tempTypeData = [];
			res.body.map((item, index) => {
				tempTypeData.push(item.type);
			})
			let filterData = [...new Set(tempTypeData)];
			filterData = filterData.filter(item => {
				return item !== ""
			});
			this.personTypeData = Object.assign([], filterData.map(item => {
				return {"text": item, "value": item}
			}));
		})
	}

	// 质量数据查询
	@action
	getAllQualityByProjectIdAndType() {
		return requestPost('getAllQualityByProjectIdAndType', Config.xmgl.getAllQualityByProjectIdAndType, Schemas.xmgl.getAllQualityByProjectIdAndType, {
			projectId: this.projectId,
			type: this.qualityType,
			pageIndex: this.qualityPageIndex,
			pageSize: this.qualityPageSize
		}).then((res) => {
			// let tempTypeData = [];
			this.qualityPageIndex = res.pageIndex;
			this.qualityPageSize = res.pageSize;
			this.qualityTotalCount = res.totalCount;
			this.qualityTableData = Object.assign([], res.body.map((item, index) => {
				// tempTypeData.push(item.type);
				Object.assign(item, {
						type: this.convertType2String(item.type),
						emergency: item.type == 1 ? this.convertEmergencyQuality2String(item.emergency) : this.convertEmergencySafe2String(item.emergency),
						state: this.convertState2String(item.state),
						key: index + 1
					}
				);
				if (index == 0) {
					return Object.assign(item, {selested: true})
				} else {
					return Object.assign(item, {selested: false})
				}
			}));

			// let filterData = [...new Set(tempTypeData)];
			// filterData = filterData.filter(item => {
			//   return item!==""
			// });
			// this.personTypeData = Object.assign([],filterData.map(item => {
			//   return {"text":item,"value":item}
			// }));

			return res;
		})
	}

