	// 获取推送设置
	@action getNotifyProject() {
		return requestPost('getNotifyProject', Config.construction.getNotifyProject, constructionSchemas.construction.getNotifyProject, {
			projectId: this.projectId,
		}).then((res) => {
			this.notifyfield = Object.assign({}, res.notifys == null ? {
				...res, ...{
					wecat: false,
					email: false
				}
			} : {...res, ...{wecat: JSON.parse(res.notifys[0]).wecat, email: JSON.parse(res.notifys[1]).email}});
			return res;
		})
	}

