const customizeRenderEmpty = () => (
	<div style={{textAlign: 'center', padding: '50px 0'}}>
		<img
			width='150px'
			height="100px"
			src='./images/not_found.png'/>
		<p style={{
			fontSize: '14px',
			fontFamily: 'PingFangSC-Regular,PingFang S',
			fontWeight: '400',
			color: 'rgba(75,80,81,1)'
		}}>暂无数据</p>
	</div>
);
const client = (self) => {
	const {token} = self.state
	// ali-oss v6.x版本的写法
	return new oss({
		accessKeyId: token.access_key_id,
		accessKeySecret: token.access_key_secret,
		stsToken: token.stsToken,
		region: token.region,
		bucket: token.bucket,
		endpoint: token.OSS_ENDPOINT
	});
};

const uploadPath = (path, file) => {
	// 上传文件的路径，使用日期命名文件目录
	return `${moment().format('YYYYMMDDHHmmss')}/${file.name}`
}

const UploadToOss = (self, path, file) => {
	const url = uploadPath(path, file)
	return new Promise((resolve, reject) => {
		client(self).multipartUpload(url, file).then(data => {
			resolve(data); //上传成功后返回的上传结果，?后面的都去掉就能正常访问了
		}).catch(error => {
			reject(error)
		})
	})
}
const openNotificationWithIcon = (type, mess) => {
	notification[type]({
		message: mess,
		description:
			'',
	});
};
const EditableContext = React.createContext();
const EditableRow = ({form, index, ...props}) => (
	<EditableContext.Provider value={form}>
		<tr {...props} />
	</EditableContext.Provider>
);
const EditableFormRow = Form.create()(EditableRow);
const {Option} = Select;
const {TextArea} = Input;
const {RangePicker} = DatePicker;

class EditableCell extends React.Component {
	state = {
		editing: true,
	};

	toggleEdit = () => {
		const editing = !this.state.editing;
		this.setState({editing}, () => {
			if (editing) {
				this.input.focus();
			}
		});
	};

	save = e => {
		const {record, handleSave} = this.props;
		this.form.validateFields((error, values) => {
			if (error && error[e.currentTarget.id]) {
				return;
			}
			delete values["createTime"];
			delete values["planSolveTime"];
			delete values["solveTime"];
			//this.toggleEdit();
			handleSave({...record, ...values});
		});
	};

	// //日期格式保存
	saveDate = (date, dateString) => {
		const {record, handleSave} = this.props;
		this.form.validateFields((error, values) => {
			if (values.hasOwnProperty('createTime')) {
				values["createTime"] = dateString;
			}
			delete values["planSolveTime"];
			delete values["solveTime"];
			handleSave({...record, ...values});
		});
	}
	saveDate2 = (date, dateString) => {
		const {record, handleSave} = this.props;
		this.form.validateFields((error, values) => {
			const keyIndex = record['state'] == 1 ? "planSolveTime" : "solveTime";
			if (values.hasOwnProperty(keyIndex)) {
				values[keyIndex] = dateString;
			}
			const keyIndex2 = keyIndex == "planSolveTime" ? "solveTime" : "planSolveTime";
			delete values["createTime"];
			delete values[keyIndex2];
			handleSave({...record, ...values});
		});
	}
	// 状态
	saveState = e => {
		const {record, handleSave} = this.props;
		this.form.validateFields((error, values) => {
			if (error && error[e.currentTarget.id]) {
				return;
			}
			if (values.hasOwnProperty('state')) {
				values["state"] = Number(e);
			}
			delete values["createTime"];
			delete values["planSolveTime"];
			delete values["solveTime"];
			//this.toggleEdit();
			handleSave({...record, ...values});
		});
	};
	//设置开始日期不能选择的时间
	disabledStartDate = startValue => {
		const {record} = this.props;
		const keyIndex = record['state'] == 1 ? "planSolveTime" : "solveTime";
		if (!startValue || !record[keyIndex]) {
			return false
		}
		return startValue.valueOf() > record[keyIndex].valueOf()
	}
	//设置结束不能选择的时间
	disabledEndDate = endValue => {
		const {record} = this.props;
		if (!endValue || !record.createTime) {
			return false
		}
		return endValue.valueOf() <= record.createTime.valueOf()
	}
	renderCell = form => {
		this.form = form;
		const {children, dataIndex, record, title, handleDelete} = this.props;
		const {editing} = this.state;
		if (dataIndex == 'dataIndex') {  // 序号
			return true ? (
				<Form.Item style={{margin: 0}}>
					{form.getFieldDecorator(dataIndex, {
						rules: [{
							required: false,
							message: `${title}是必输`,
						}],
						initialValue: record[dataIndex],
					})(
						<div className="pr" style={{
							width: '95px',
							margin: '0 auto'
						}}>
							<Popconfirm title="确定要删除吗?" onConfirm={() => handleDelete(record.id)}>
								<Icon
									style={{
										width: '35px',
										color: '#F17E7E',
										fontSize: '18px',
										position: 'absolute',
										left: '-8px',
										top: '8px'
									}}
									type="minus-circle"/>
							</Popconfirm>
							<div
								className="editable-cell-value-wrap"
								style={{
									minHeight: '31px'
								}}
							>
								{record[dataIndex]}
							</div>
						</div>
					)}
				</Form.Item>
			) : (
				<div className="w clearfix">
					<Popconfirm title="确定要删除吗?" onConfirm={() => handleDelete(record)}>
						<Icon
							style={{
								width: '35px',
								color: '#F17E7E',
								fontSize: '18px',
								float: 'left'
							}}
							type="minus-circle"/>
					</Popconfirm>
					<div
						className="editable-cell-value-wrap"
						style={{
							float: 'right',
							width: 'calc(100% - 35px)',
							minHeight: '31px'
						}}
					>
						{children}
					</div>
				</div>
			);
		} else if (dataIndex == 'createTime') { // 发现时间
			// 日期时间戳转换字符串
			return editing ? (<Form.Item style={{margin: 0}}>
				{form.getFieldDecorator(dataIndex, {
					rules: [
						{
							required: false,
							message: `${title}是必输`,
						},
					],
					initialValue: record[dataIndex] == null || record[dataIndex] == 0 ? "" : moment(record[dataIndex], "YYYY-MM-DD")
				})(
					<DatePicker
						style={{width: '120px'}}
						format="YYYY-MM-DD"
						ref={node => (this.input = node)}
						onChange={this.saveDate}
						//设置开始日期
						//disabledDate={this.disabledStartDate}
					/>
				)}
			</Form.Item>) : (<div
					className="editable-cell-value-wrap"
					style={{minHeight: '31px'}}
					//onClick={this.toggleEdit}
				>
					{children}
				</div>
			);
		} else if (dataIndex == 'state') { //  问题状态
			return editing ? (
				<Form.Item style={{margin: 0}}>
					{form.getFieldDecorator(dataIndex, {
						rules: [{
							required: false,
							message: `${title}是必输`,
						}],
						initialValue: record[dataIndex].toString(),
					})(
						<Select
							ref={node => (this.input = node)}
							onChange={this.saveState}>
							<Option value="1">发现问题</Option>
							<Option value="2">整改完成</Option>
						</Select>
					)}
				</Form.Item>
			) : (
				<div
					className="editable-cell-value-wrap"
					style={{minHeight: '31px'}}
					//onClick={this.toggleEdit}
				>
					{children}
				</div>
			);

		} else if (dataIndex == 'planSolveTime') { //  解决时间
			const keyIndex = record['state'] == 1 ? "planSolveTime" : "solveTime";
			// 问题状态联动
			const numberTostring = record[keyIndex] == null ? "" : record[keyIndex];
			return editing ? (<Form.Item style={{margin: 0}}>
				{form.getFieldDecorator(keyIndex, {
					rules: [
						{
							required: false,
							message: `${title}是必输`,
						},
					],
					initialValue: record[keyIndex] == null ? "" : moment(numberTostring, "YYYY-MM-DD")
				})(
					<DatePicker
						style={{width: '120px'}}
						format="YYYY-MM-DD"
						ref={node => (this.input = node)}
						onChange={this.saveDate2}
						//设置结束不能选择的时间
						//disabledDate={this.disabledEndDate}
					/>
				)}
			</Form.Item>) : (<div
					className="editable-cell-value-wrap"
					style={{minHeight: '31px'}}
					//onClick={this.toggleEdit}
				>
					{children}
				</div>
			);
		} else if (dataIndex == 'reviewer') {
			return editing ? (<Form.Item style={{margin: 0}}>
					{form.getFieldDecorator(dataIndex, {
						rules: [{
							required: false,
							message: `${title}是必输`,
						}],
						initialValue: record[dataIndex] == null || record[dataIndex] == 0 ? "" : record[dataIndex],
					})(
						<Input
							ref={node => (this.input = node)}
							onChange={this.save}
							onBlur={this.save}
						/>
					)}
				</Form.Item>
			) : (<div
					className="editable-cell-value-wrap"
					style={{minHeight: '31px'}}
					//onClick={this.toggleEdit}
				>
					{children}
				</div>
			);
		} else {
			return editing ? (<Form.Item style={{margin: 0}}>
					{form.getFieldDecorator(dataIndex, {
						rules: [{
							required: false,
							message: `${title}是必输`,
						}],
						initialValue: record[dataIndex] == null || record[dataIndex] == 0 ? "" : record[dataIndex],
					})(
						<TextArea rows={2}
						          ref={node => (this.input = node)}
						          onChange={this.save}
						          onBlur={this.save}
						/>
					)}
				</Form.Item>
			) : (<div
					className="editable-cell-value-wrap"
					style={{minHeight: '31px'}}
					//onClick={this.toggleEdit}
				>
					{children}
				</div>
			);
		}
	};

	render() {
		const {
			editable,
			dataIndex,
			title,
			record,
			index,
			handleSave,
			children,
			...restProps
		} = this.props;
		return (
			<td {...restProps}>
				{editable ? (
					<EditableContext.Consumer>{this.renderCell}</EditableContext.Consumer>
				) : (
					children
				)}
			</td>
		);
	}
}

class EditableTagGroup extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			tags: [],
			inputVisible: false,
			inputValue: '',
		}
	}

	componentWillReceiveProps(nextProps) {
		this.setState({
			tags: nextProps.data
		})
	}

	handleClose = removedTag => {
		const tags = this.state.tags.filter(tag => tag !== removedTag);
		this.setState({tags});
	};

	showInput = () => {
		this.setState({inputVisible: true}, () => this.input.focus());
	};

	handleInputChange = e => {
		this.setState({inputValue: e.target.value});
	};

	handleInputConfirm = () => {
		const {inputValue} = this.state;
		if (inputValue !== "" && !(/^[a-zA-Z0-9.!#$%&'*+\/=?^_`{|}~-]+@[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?(?:\.[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?)*$/.test(inputValue))) {
			message.warn("请输入正确的邮箱格式");
			return false;
		}
		let {tags} = this.state;
		if (inputValue && tags.indexOf(inputValue) === -1) {
			tags = [...tags, inputValue];
		}
		this.setState({
			tags,
			inputVisible: false,
			inputValue: '',
		}, () => {
			this.props.updateState(tags)
		});
	};

	saveInputRef = input => (this.input = input);

	render() {
		const {tags, inputVisible, inputValue} = this.state;

		return (
			<div>
				{tags.map((tag, index) => {
					const isLongTag = tag.length > 20;
					const tagElem = (
						<Tag key={tag} closable={index !== 0} onClose={() => this.handleClose(tag)}>
							{isLongTag ? `${tag.slice(0, 20)}...` : tag}
						</Tag>
					);
					return isLongTag ? (
						<Tooltip title={tag} key={tag}>
							{tagElem}
						</Tooltip>
					) : (
						tagElem
					);
				})}
				{inputVisible && (
					<Input
						ref={this.saveInputRef}
						type="email"
						size="small"
						style={{width: 200}}
						value={inputValue}
						onChange={this.handleInputChange}
						onBlur={this.handleInputConfirm}
						onPressEnter={this.handleInputConfirm}
					/>
				)}
				{!inputVisible && (
					<Tag onClick={this.showInput} style={{background: '#fff', borderStyle: 'dashed'}}>
						<Icon type="plus"/> 添加收件人地址
					</Tag>
				)}
			</div>
		);
	}
}
@observer
class Demo extends React.Component {
	constructor(props) {
		super(props);
		this.store = new XmglStore()
		this.state = {
			person: [], // 人员
			token: {
				access_key_id: '', // oss的key_id
				access_key_secret: '', // oss的secret
				stsToken: '',
				region: 'oss-cn-beijing', // oss的region
				bucket: 'xinlj', // oss的bucket
				OSS_ENDPOINT: 'oss-cn-beijing.aliyuncs.com',  // 自己oss服务器的配置信息
			},
			host: 'https://xinlj.oss-cn-beijing.aliyuncs.com/', // 阿里云host
			pdfUrl: '',  // 生成pdf地址
			imgUrl: '',  // 生成截图地址
			zplanTasksloading: false,
			nextZplanTasksloading: false,
			tip: '正在生成请稍等',
			isLoading: false
		}
		this.setTextInputRef = element => {
			this.textInput = element;
		};
	}

	componentWillMount() {

	}

	componentDidMount() {
		let search = this.props.location.search;
		const projectId = url.parse(search, true).query.projectId;
		const parojectName = url.parse(search, true).query.name;
		if (projectId !== undefined) {
			let newReport = JSON.parse(JSON.stringify(this.store.newReport));
			this.store.newReport = Object.assign(newReport, {
				"projectId": projectId,
				"projectName": parojectName
			});
			localStorage.setItem("projectId", projectId);
			localStorage.setItem("projectName", parojectName);
		} else {
			let newReport = JSON.parse(JSON.stringify(this.store.newReport));
			this.store.newReport = Object.assign(newReport, {
				"projectId": localStorage.getItem("projectId"),
				"projectName": localStorage.getItem("projectName")
			});
			this.props.form.setFieldsValue({
				"projectName": localStorage.getItem("projectName")
			});
		}
		this.store.getRole().then((res) => {
			this.setState({
				person: res.body
			});
		});
		// 获取大文件上传sts
		this.store.getSts().then((res) => {
			let str = typeof res == "string" ? JSON.parse(res) : res;
			if (str) {
				let token = JSON.parse(JSON.stringify(this.state.token));
				this.setState({
					token: Object.assign(token, {
						access_key_id: str.accessKeyId,         // oss的key_id
						access_key_secret: str.accessKeySecret, // oss的secret
						stsToken: str.securityToken
					})
				});
			}
		});
	}

	componentWillUnmount() {
		clearInterval(this.timer);
	}

	// 上传图片
	uploadImg = () => {
		let _this = this;
		let newReport = JSON.parse(JSON.stringify(_this.store.newReport));
		const {personDates} = newReport;
		const isPerson = personDates == null ? [] : personDates;
		if (isPerson.length == 0) {
			let newReport = JSON.parse(JSON.stringify(_this.store.newReport));
			const {emailUserIds} = newReport;
			const params = {
				report: Object.assign(newReport, {
					emailUserIds: emailUserIds == null ? [] : emailUserIds.map(item => item.userId),
					qualities: newReport.qualities == null ? [] : newReport.qualities.map((item) => {
						delete item.dataIndex;
						return item;
					}),
					attachments: newReport.attachments == null ? [] : newReport.attachments.map(item => Object.assign({
						fileId: item.fileId,
						fileName: item.fileName,
						filePath: item.filePath
					})),
					nextAttachments: newReport.nextAttachments == null ? [] : newReport.nextAttachments.map(item => Object.assign({
						fileId: item.fileId,
						fileName: item.fileName,
						filePath: item.filePath
					})),
				}),
				imgUrl: ""
			}
			_this.store.downloadReport(params)
			.then((res) => {
				const {id} = res;
				this.getReportUrl(id);
			});
		} else {
			let echarts_instance = _this.echarts_react.getEchartsInstance();
			let base64 = echarts_instance.getDataURL();

			//将base64转换为文件对象
			const dataURLtoFile = (dataurl, filename) => {
				var arr = dataurl.split(',');
				var mime = arr[0].match(/:(.*?);/)[1];
				var bstr = atob(arr[1]);
				var n = bstr.length;
				var u8arr = new Uint8Array(n);
				while (n--) {
					u8arr[n] = bstr.charCodeAt(n);
				}
				//转换成file对象
				return new File([u8arr], filename, {type: mime});
				//转换成成blob对象
				// return new Blob([u8arr],{type:mime});
			};

			var myfile2 = dataURLtoFile(base64, '施工周报.png');
			var formData2 = new FormData();
			formData2.append('newFile', myfile2);
			let reader2 = new FileReader();
			reader2.readAsDataURL(myfile2);
			reader2.onloadend = () => {
				// 使用ossupload覆盖默认的上传方法
				UploadToOss(_this, '上传路径oss配置信息', myfile2)
				.then(data => {
					const {host} = _this.state;
					_this.setState({
						imgUrl: host + data.name
					}, () => {
						let newReport = JSON.parse(JSON.stringify(_this.store.newReport));
						const {emailUserIds} = newReport;
						const params = {
							report: Object.assign(newReport, {
								emailUserIds: emailUserIds == null ? [] : emailUserIds.map(item => item.userId),
								qualities: newReport.qualities == null ? [] : newReport.qualities.map((item) => {
									delete item.dataIndex;
									return item;
								}),
								attachments: newReport.attachments == null ? [] : newReport.attachments.map(item => Object.assign({
									fileId: item.fileId,
									fileName: item.fileName,
									filePath: item.filePath
								})),
								nextAttachments: newReport.nextAttachments == null ? [] : newReport.nextAttachments.map(item => Object.assign({
									fileId: item.fileId,
									fileName: item.fileName,
									filePath: item.filePath
								})),
							}),
							imgUrl: _this.state.imgUrl
						}
						_this.store.downloadReport(params)
						.then((res) => {
							const {id} = res;
							this.getReportUrl(id);
						});
					});
				});
			}
		}
	}

	// 间隔3秒获取pdf地址
	getReportUrl = (id) => {
		let _this = this;
		const params = {
			id: id
		}
		this.setState({
			isLoading: true
		});
		let newReport = JSON.parse(JSON.stringify(_this.store.newReport));
		const {projectName, beginTime, endTime} = newReport;
		const pdfName = projectName + moment(beginTime).format("YYYY-MM-DD") + "到" + moment(endTime).format("YYYY-MM-DD") + "施工情况汇报";

		_this.timer = setInterval(() => {
			_this.store.getReportUrl(params).then((res) => {
				const {url} = res;
				if (url !== null && url !== "") {
					this.setState({
						isLoading: false
					});
					downloadFile(url, pdfName);  // 直接下载pdf
					clearInterval(_this.timer);
				}
			});
		}, 3000);
	}

	// 该函数暂不使用
	download = () => {
		let _this = this;
		html2canvas(document.body, {
			useCORS: true,
			allowTaint: true,
			scale: 2 // 提升画面质量，但是会增加文件大小
		}).then(function (canvas) {
			var contentWidth = canvas.width;
			var contentHeight = canvas.height;
			//一页pdf显示html页面生成的canvas高度;
			var pageHeight = contentWidth / 592.28 * 841.89;
			//未生成pdf的html页面高度
			var leftHeight = contentHeight;
			//页面偏移
			var position = 0;
			//a4纸的尺寸[595.28,841.89]，html页面生成的canvas在pdf中图片的宽高
			var imgWidth = 595.28;
			var imgHeight = 592.28 / contentWidth * contentHeight;
			var pageData = canvas.toDataURL('image/jpeg', 1.0);
			var pdf = new jsPDF('', 'pt', 'a4');
			//有两个高度需要区分，一个是html页面的实际高度，和生成pdf的页面高度(841.89)
			//当内容未超过pdf一页显示的范围，无需分页
			if (leftHeight < pageHeight) {
				pdf.addImage(pageData, 'JPEG', 0, 0, imgWidth, imgHeight);
			} else {
				while (leftHeight > 0) {
					pdf.addImage(pageData, 'JPEG', 0, position, imgWidth, imgHeight)
					leftHeight -= pageHeight;
					position -= 841.89;
					//避免添加空白页
					if (leftHeight > 0) {
						pdf.addPage();
					}
				}
			}
			pdf.save('施工周报.pdf');

			//需求一：将生成好的pdf上传至阿里云oss
			//将base64转换为文件对象
			const dataURLtoFile = (dataurl, filename) => {
				var arr = dataurl.split(',');
				var mime = arr[0].match(/:(.*?);/)[1];
				var bstr = atob(arr[1]);
				var n = bstr.length;
				var u8arr = new Uint8Array(n);
				while (n--) {
					u8arr[n] = bstr.charCodeAt(n);
				}
				//转换成file对象
				return new File([u8arr], filename, {type: mime});
				//转换成成blob对象
				// return new Blob([u8arr],{type:mime});
			};
			// 将pdf输入为base格式的字符串
			var buffer = pdf.output("datauristring");
			// 将base64格式的字符串转换为file文件
			var myfile = dataURLtoFile(buffer, '施工周报.pdf');
			var formData = new FormData();
			formData.append('newFile', myfile);
			// 生成成功后直传oss
			let reader = new FileReader();
			reader.readAsDataURL(myfile);
			reader.onloadend = () => {
				// 使用ossupload覆盖默认的上传方法
				UploadToOss(_this, '上传路径oss配置信息', myfile)
				.then(data => {
					const {host} = _this.state;
					_this.setState({
						pdfUrl: host + data.name
					});
				});
			}

			//需求二：将截图上传至阿里云oss
			var imgUri = canvas.toDataURL("image/png").replace("image/png", "image/octet-stream"); // 获取生成的图片的url 　
			// 生成成功后直传oss
			var myfile2 = dataURLtoFile(imgUri, '施工周报.png');
			var formData2 = new FormData();
			formData2.append('newFile', myfile2);
			let reader2 = new FileReader();
			reader2.readAsDataURL(myfile2);
			reader2.onloadend = () => {
				// 使用ossupload覆盖默认的上传方法
				UploadToOss(_this, '上传路径oss配置信息', myfile2)
				.then(data => {
					const {host} = _this.state;
					_this.setState({
						imgUrl: host + data.name
					});
				});
			}
		})
	};

	// 斑马
	handleAttment = (type, params) => {
		let newReport = JSON.parse(JSON.stringify(this.store.newReport));
		// 处理附件
		let arr = [];
		params.length && params.map((item) => {
			arr.push({
				fileId: item.fileId,
				fileName: item.fileName,
				filePath: item.filePath,
				id: item.id,
				uid: item.uid,
				name: item.fileName,
				status: 'done',
				url: item.filePath,
			})
		});
		this.store.newReport = Object.assign(newReport, {
			[type]: Object.assign([], arr)
		});
	}

	// 一键同步
	handAsync = (type) => {
		this.setState({
			[type + 'loading']: true
		}, () => {
			this.store.getNewReport().then((res) => {
				if (res) {
					let newReport = JSON.parse(JSON.stringify(this.store.newReport));
					this.store.newReport = Object.assign(newReport, {
						[type]: res[type]
					});
				}
				this.setState({
					[type + 'loading']: false
				});
			});
		});
	}

	// 增加一行
	handleAdd = () => {
		let newReport = JSON.parse(JSON.stringify(this.store.newReport));
		const {qualities} = newReport;
		const primaryData = {
			"id": "",
			"question": "",
			"createTime": null,
			"state": 1,
			"solveTime": null,
			"handler": "",
			"reviewer": "",
			"remark": "",
			"updateTime": null,
			"planSolveTime": null
		}
		const newData = Object.assign(primaryData, {dataIndex: qualities.length + 1, id: uuidv4()})
		this.store.newReport = Object.assign(newReport, {
			qualities: [...qualities, newData]
		});
	};

	// 删除一行
	handleDelete = (id) => {
		let newReport = JSON.parse(JSON.stringify(this.store.newReport));
		const {qualities} = newReport;
		const newQualities = qualities.filter(item => item.id !== id);
		this.store.newReport = Object.assign(newReport, {
			qualities: newQualities.map((vitem, vindex) => Object.assign(vitem, {dataIndex: vindex + 1}))
		});
	}

	updateState = (type, data) => {
		let newReport = JSON.parse(JSON.stringify(this.store.newReport));
		this.store.newReport = Object.assign(newReport, {
			[type]: Object.assign([], data)
		});
	}

	onChangeDate = (type, dates, dateStrings) => {
		let newReport = JSON.parse(JSON.stringify(this.store.newReport));
		if (type == "range") {
			this.store.newReport = Object.assign(newReport, {
				"beginTime": moment(dateStrings[0]).valueOf(),
				"endTime": moment(dateStrings[1]).valueOf(),
			});
			this.store.getNewReport().then((res) => {
				if (res) {
					this.store.newReport = Object.assign({},
						{
							"projectId": res.projectId,
							"projectName": res.projectName,
							"beginTime": res.beginTime,
							"endTime": res.endTime,
							"nextBeginTime": res.nextBeginTime,
							"nextEndTime": res.nextEndTime,
							"keyIssues": res.keyIssues == null ? "" : res.keyIssues,
							"personDates": res.personDates == null ? [] : res.personDates,
							"materialApproach": res.materialApproach == null ? "" : res.materialApproach,
							"materials": res.materials == null ? [] : res.materials,
							"attachments": res.attachments == null ? [] : res.attachments,
							"nextAttachments": res.nextAttachments == null ? [] : res.nextAttachments,
							"zplanTasks": res.zplanTasks == null ? [] : res.zplanTasks,
							"nextZplanTasks": res.nextZplanTasks == null ? [] : res.nextZplanTasks,
							"qualities": res.qualities == null ? [] : res.qualities.map((item, index) => Object.assign(item, {dataIndex: index + 1})),
							"positions": res.positions == null ? [] : res.positions,
							"emails": res.emails == null ? [] : res.emails,
							"emailUserIds": res.emailUserIds == null ? [] : res.emailUserIds,
						});
				}
			});
		} else if (type == "nextpicker") {
			this.store.newReport = Object.assign(newReport, {
				"nextBeginTime": moment(dateStrings[0]).valueOf(),
				"nextEndTime": moment(dateStrings[1]).valueOf(),
			});
			this.store.getNewReport().then((res) => {
				if (res) {
					let newReport = JSON.parse(JSON.stringify(this.store.newReport));
					this.store.newReport = Object.assign(newReport, {
						nextZplanTasks: res["nextZplanTasks"]
					});
				}
			});
		}
	}

	onChange = (type, e) => {
		let newReport = JSON.parse(JSON.stringify(this.store.newReport));
		this.store.newReport = Object.assign(newReport, {
			[type]: e.target.value
		});
	}

	// 斑马表格
	handleBzSave = (row) => {
		let newReport = JSON.parse(JSON.stringify(this.store.newReport));
		const zplanTasks = newReport.zplanTasks;
		const index = zplanTasks.findIndex(item => row.id === item.id);
		const item = zplanTasks[index];
		zplanTasks.splice(index, 1, {
			...item,
			"remark": row.remark
		});
		this.store.newReport = Object.assign(newReport, {
			['zplanTasks']: zplanTasks
		});
	}

	// 质量安全表格
	handleSave = (row) => {
		let newReport = JSON.parse(JSON.stringify(this.store.newReport));
		const zplanTasks = newReport.qualities;
		const index = zplanTasks.findIndex(item => row.dataIndex === item.dataIndex);
		const item = zplanTasks[index];
		zplanTasks.splice(index, 1, {
			...item,
			...row
		});
		this.store.newReport = Object.assign(newReport, {
			"qualities": zplanTasks
		});
	}

	// 发送邮件
	sendReport = () => {
		let _this = this;
		const {imgUrl} = this.state;
		let newReport = JSON.parse(JSON.stringify(this.store.newReport));
		const {emails, emailUserIds, personDates} = newReport;
		const isemailUserIds = emailUserIds == null ? [] : emailUserIds;
		const isemails = emails == null ? [] : emails;
		// 选择收件人
		if (isemailUserIds.length == 0 && isemails.length == 0) {
			openNotificationWithIcon('warn', "请先选择邮件收件人");
			return false;
		}
		const isPerson = personDates == null ? [] : personDates;
		if (isPerson.length == 0) {
			const params = {
				report: Object.assign(newReport, {
					emailUserIds: emailUserIds == null ? [] : emailUserIds.map(item => item.userId),
					qualities: newReport.qualities == null ? [] : newReport.qualities.map((item) => {
						delete item.dataIndex;
						return item;
					}),
					attachments: newReport.attachments == null ? [] : newReport.attachments.map(item => Object.assign({
						fileId: item.fileId,
						fileName: item.fileName,
						filePath: item.filePath
					})),
					nextAttachments: newReport.nextAttachments == null ? [] : newReport.nextAttachments.map(item => Object.assign({
						fileId: item.fileId,
						fileName: item.fileName,
						filePath: item.filePath
					})),
				}),
				imgUrl: imgUrl
			}
			this.store.sendReportEmail(params)
			.then((res) => {
				if (res == true) {
					message.success("邮件发送成功")
				}
			});
		} else {
			if (this.state.imgUrl == "") {  // 未保存pdf
				let echarts_instance = _this.echarts_react.getEchartsInstance();
				let base64 = echarts_instance.getDataURL();

				//将base64转换为文件对象
				const dataURLtoFile = (dataurl, filename) => {
					var arr = dataurl.split(',');
					var mime = arr[0].match(/:(.*?);/)[1];
					var bstr = atob(arr[1]);
					var n = bstr.length;
					var u8arr = new Uint8Array(n);
					while (n--) {
						u8arr[n] = bstr.charCodeAt(n);
					}
					//转换成file对象
					return new File([u8arr], filename, {type: mime});
					//转换成成blob对象
					// return new Blob([u8arr],{type:mime});
				};

				var myfile2 = dataURLtoFile(base64, '施工周报.png');
				var formData2 = new FormData();
				formData2.append('newFile', myfile2);
				let reader2 = new FileReader();
				reader2.readAsDataURL(myfile2);
				reader2.onloadend = () => {
					// 使用ossupload覆盖默认的上传方法
					UploadToOss(_this, '上传路径oss配置信息', myfile2)
					.then(data => {
						const {host} = _this.state;
						_this.setState({
							imgUrl: host + data.name
						}, () => {
							let newReport = JSON.parse(JSON.stringify(_this.store.newReport));
							const {emailUserIds} = newReport;
							const params = {
								report: Object.assign(newReport, {
									emailUserIds: emailUserIds == null ? [] : emailUserIds.map(item => item.userId),
									qualities: newReport.qualities == null ? [] : newReport.qualities.map((item) => {
										delete item.dataIndex;
										return item;
									}),
									attachments: newReport.attachments == null ? [] : newReport.attachments.map(item => Object.assign({
										fileId: item.fileId,
										fileName: item.fileName,
										filePath: item.filePath
									})),
									nextAttachments: newReport.nextAttachments == null ? [] : newReport.nextAttachments.map(item => Object.assign({
										fileId: item.fileId,
										fileName: item.fileName,
										filePath: item.filePath
									})),
								}),
								imgUrl: _this.state.imgUrl
							}
							_this.store.sendReportEmail(params)
							.then((res) => {
								if (res == true) {
									message.success("邮件发送成功")
								}
							});
						});
					});
				}
			} else { //已生成pdf
				const params = {
					report: Object.assign(newReport, {
						emailUserIds: emailUserIds == null ? [] : emailUserIds.map(item => item.userId),
						qualities: newReport.qualities == null ? [] : newReport.qualities.map((item) => {
							delete item.dataIndex;
							return item;
						}),
						attachments: newReport.attachments == null ? [] : newReport.attachments.map(item => Object.assign({
							fileId: item.fileId,
							fileName: item.fileName,
							filePath: item.filePath
						})),
						nextAttachments: newReport.nextAttachments == null ? [] : newReport.nextAttachments.map(item => Object.assign({
							fileId: item.fileId,
							fileName: item.fileName,
							filePath: item.filePath
						})),
					}),
					imgUrl: imgUrl
				}
				this.store.sendReportEmail(params)
				.then((res) => {
					if (res == true) {
						message.success("邮件发送成功")
					}
				});
			}
		}
	}

	render() {
		let pathname = this.props.match.path;
		const {ossData} = this.store;
		const {getFieldDecorator} = this.props.form;
		const formItemLayout = {
			labelCol: {span: 4},
			wrapperCol: {span: 18},
		};
		const rangeConfig = {
			rules: [{type: 'array', required: false, message: 'Please select time!'}],
		};
		const materColumns = [
			{
				title: '进场日期',
				dataIndex: 'deliveryTime',
				key: 'deliveryTime',
				align: 'center'
			},
			{
				title: '材料名称',
				dataIndex: 'name',
				key: 'name',
				align: 'center'
			},
			{
				title: '数量',
				dataIndex: 'totalNum',
				key: 'totalNum',
				align: 'center'
			},
			{
				title: '单位',
				dataIndex: 'units',
				key: 'units',
				align: 'center'
			}]
		const columns = [
			{
				title: '开始时间',
				dataIndex: 'beginTime',
				key: 'beginTime',
				align: 'center',
				render: text => <span>{moment(text).format("YYYY-MM-DD")}</span>,
			},
			{
				title: '截止时间',
				dataIndex: 'endTime',
				key: 'endTime',
				align: 'center',
				render: text => <span>{moment(text).format("YYYY-MM-DD")}</span>,
			},
			{
				title: '任务名称',
				dataIndex: 'name',
				key: 'name',
				align: 'center',
			},
			{
				title: '阶段完成率',
				dataIndex: 'rate',
				key: 'rate',
				align: 'center',
			},
			{
				title: '备注',
				dataIndex: 'remark',
				key: 'remark',
				align: 'center',
				editable: true,
			}
		];
		const nextColumns = [
			{
				title: '开始时间',
				dataIndex: 'beginTime',
				key: 'beginTime',
				align: 'center',
				render: text => <span>{moment(text).format("YYYY-MM-DD")}</span>,
			},
			{
				title: '截止时间',
				dataIndex: 'endTime',
				key: 'endTime',
				align: 'center',
				render: text => <span>{moment(text).format("YYYY-MM-DD")}</span>,
			},
			{
				title: '任务名称',
				dataIndex: 'name',
				key: 'name',
				align: 'center',
			}]
		const massColumns = [
			{
				title: '序号',
				dataIndex: 'dataIndex',
				key: 'dataIndex',
				align: 'center',
				editable: true,
				width: '95px',
			},
			{
				title: '发现时间',
				dataIndex: 'createTime',
				key: 'createTime',
				align: 'center',
				width: '120px',
				editable: true,
				render: text => <span>{moment(text).format("YYYY-MM-DD")}</span>,
			},
			{
				title: '问题描述',
				dataIndex: 'question',
				key: 'question',
				align: 'center',
				width: '15%',
				editable: true,
			},
			{
				title: '处理方案',
				dataIndex: 'remark',
				key: 'remark',
				align: 'center',
				width: '15%',
				editable: true,
			},
			{
				title: '问题状态',
				dataIndex: 'state',
				key: 'state',
				align: 'center',
				width: '15%',
				editable: true,
				render: (text, record) => <span>
					{record.state == "1" ? "发现问题" : "整改完成"}
				</span>
			},
			{
				title: '计划/实际解决时间',
				dataIndex: 'planSolveTime',
				key: 'planSolveTime',
				align: 'center',
				width: '120px',
				editable: true,
				render: (text, record) => <span>
						{record.state == "1" ? moment(record.planSolveTime).format("YYYY-MM-DD") : moment(record.solveTime).format("YYYY-MM-DD")}
				</span>
			},
			{
				title: '验收人',
				dataIndex: 'reviewer',
				key: 'reviewer',
				align: 'center',
				width: '15%',
				editable: true,
			}
		]
		const components = {
			body: {
				row: EditableFormRow,
				cell: EditableCell,
			},
		};
		const colu = columns.map(col => {
			if (!col.editable) {
				return col;
			}
			return {
				...col,
				onCell: record => ({
					record,
					editable: col.editable,
					dataIndex: col.dataIndex,
					title: col.title,
					handleSave: this.handleBzSave,
				}),
			};
		});

		const massColu = massColumns.map(col => {
			if (!col.editable) {
				return col;
			}
			return {
				...col,
				onCell: record => ({
					record,
					editable: col.editable,
					dataIndex: col.dataIndex,
					title: col.title,
					handleSave: this.handleSave,
					handleDelete: this.handleDelete
				}),
			};
		});
		const {newReport} = this.store;
		const fields = mobx.toJS(newReport);
		var option = {
			title: {},
			tooltip: {},
			backgroundColor: '#FFF',
			xAxis: {
				type: 'category',
				axisTick: {
					alignWithLabel: true
				},
				data: fields.personDates.map(item => moment(item.date).format("YYYYMMDD"))
			},
			yAxis: {
				axisTick: { //y轴刻度线
					show: false
				},
				axisLine: { //y轴
					show: false
				}
			},
			series: [{
				name: '人员情况',
				type: 'line',
				// 显示数值
				itemStyle: {
					normal: {
						label: {
							show: true,
							textStyle: {
								fontSize: 22
							},
						}
					}
				},
				data: fields.personDates.map(item => item.count),
				sampling: 'average',
				showAllSymbol: true,
				areaStyle: {
					normal: {
						//颜色渐变函数 前四个参数分别表示四个位置依次为左、下、右、上
						color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
							offset: 0,
							color: 'rgb(255,153,106, 0.89)'
						}, {
							offset: .34,
							color: 'rgb(255,153,106, 0.45)'
						}, {
							offset: 1,
							color: 'rgb(255,153,106, 0.00)'
						}])
					}
				},//区域颜色渐变
			}]
		};
		return (
			<div>
			<div className="con_bottom">
				<Breadcrumb separator=">" style={{margin: '0'}}>
					<Breadcrumb.Item href="#/bpm/construction">施工过程</Breadcrumb.Item>
					<Breadcrumb.Item>周报</Breadcrumb.Item>
				</Breadcrumb>
			</div>
				<Form className="mt10" id="demo" {...formItemLayout} onSubmit={this.handleSubmit} style={{padding: '15px 15px 50px'}}>
					<Form.Item label="项目名称" ref={this.setTextInputRef} className="personchart">
						{getFieldDecorator('projectName', {
							initialValue: fields.projectName,
						})(<Input placeholder="请输入" onChange={this.onChange.bind(this, 'projectName')}
						          className="mb10"/>)}
					</Form.Item>
					<Form.Item label="选择日期">
						{getFieldDecorator('range-picker', rangeConfig)(
							<div className="mb10">
								<RangePicker onChange={this.onChangeDate.bind(this, 'range')}/>
							</div>
						)}
					</Form.Item>
					<Form.Item label="重点事项">
						{getFieldDecorator('keyIssues', {
							initialValue: fields.keyIssues == null ? "" : fields.keyIssues,
						})(
							<TextArea
								className="mb10"
								placeholder="请输入"
								onChange={this.onChange.bind(this, 'keyIssues')}
								autoSize={{minRows: 2, maxRows: 6}}
							/>)}
					</Form.Item>
					<Form.Item label="人员进场">
						{fields.personDates.length > 0 ? <ReactEcharts
							ref={(e) => {
								this.echarts_react = e;
							}}
							notMerge={true}
							lazyUpdate={true}
							option={option}/> : null}
					</Form.Item>
					<Form.Item label="材料进场">
						{getFieldDecorator('materialApproach', {
							initialValue: fields.materialApproach == null ? "" : fields.materialApproach,
							rules: [{required: false, message: '请输入材料进场'}],
						})(<Input placeholder="请描述材料已到场情况和剩余材料到场计划"
						          onChange={this.onChange.bind(this, 'materialApproach')} className="mb10"/>)}
					</Form.Item>
					<Form.Item wrapperCol={{span: 18, offset: 4}}>
						<ConfigProvider renderEmpty={customizeRenderEmpty}>
							<Table pagination={false}
							       columns={materColumns}
							       size={"middle"}
							       dataSource={fields.materials}/>
						</ConfigProvider>
					</Form.Item>
					<Form.Item label="本阶段计划及完成情况" colon={false} className="nowStage"></Form.Item>
					<Form.Item label="斑马进度图" colon={false}>
						{getFieldDecorator('attachments', {})(
							<MyUpload
								length={1}
								saveAttachment={this.handleAttment.bind(this, 'attachments')}
								disabled={false}
								multiple={true}
								listType="picture-card"
								ossData={mobx.toJS(ossData)}
								fileList={fields.attachments == null ? [] : fields.attachments}
							/>
						)}
					</Form.Item>
					<Form.Item label="任务完成情况" colon={false}>
						<Button type="link" onClick={this.handAsync.bind(this, 'zplanTasks')}>一键同步</Button>
					</Form.Item>
					<Form.Item wrapperCol={{span: 18, offset: 4}}>
						<ConfigProvider renderEmpty={customizeRenderEmpty}>
							<Table
								components={components}
								rowClassName={() => 'editable-row'}
								className="mt10"
								pagination={false}
								columns={colu}
								size={"middle"}
								loading={this.state.zplanTasksloading}
								dataSource={fields.zplanTasks}
							/>
						</ConfigProvider>
					</Form.Item>
					<Form.Item label="下阶段计划" colon={false} className="nextStage"></Form.Item>
					<Form.Item label="选择日期">
						{getFieldDecorator('nextpicker', rangeConfig)(<div className="mb10">
							<RangePicker onChange={this.onChangeDate.bind(this, 'nextpicker')}/>
						</div>)}
					</Form.Item>
					<Form.Item label="斑马进度图" colon={false}>
						<MyUpload
							length={1}
							saveAttachment={this.handleAttment.bind(this, 'nextAttachments')}
							disabled={false}
							multiple={true}
							listType="picture-card"
							ossData={mobx.toJS(ossData)}
							fileList={fields.nextAttachments == null ? [] : fields.nextAttachments}
						/>
					</Form.Item>
					<Form.Item label="任务计划情况" colon={false}>
						<Button type="link" onClick={this.handAsync.bind(this, 'nextZplanTasks')}>一键同步</Button>
					</Form.Item>
					<Form.Item wrapperCol={{span: 10, offset: 4}}>
						<ConfigProvider renderEmpty={customizeRenderEmpty}>
							<Table className="mt10"
							       pagination={false}
							       columns={nextColumns}
							       size={"middle"}
							       loading={this.state.nextZplanTasksloading}
							       dataSource={fields.nextZplanTasks}/>
						</ConfigProvider>
					</Form.Item>
					<Form.Item label="质量安全问题及处理方案" colon={false} className="nextStage"></Form.Item>
					<Form.Item wrapperCol={{span: 18, offset: 4}}>
						<ConfigProvider renderEmpty={customizeRenderEmpty}>
							<Table
								components={components}
								rowClassName={() => 'editable-row'}
								className="mt10"
								pagination={false}
								columns={massColu}
								dataSource={fields.qualities}
								size={"middle"}
							/>
						</ConfigProvider>
						<Icon onClick={this.handleAdd}
						      style={{cursor: 'pointer', margin: '10px 0', color: '#4BA4BE', fontSize: '22px'}}
						      type="plus-circle"/>
					</Form.Item>
					<Form.Item label="定点照片" colon={false} className="nextStage"></Form.Item>
					{
						fields.positions.map((item, index) => {
							return (<div key={index}>
									<Form.Item label="位置点名称" colon={false}>
										<Input value={item.name} style={{width: '250px'}} disabled={true}/>
									</Form.Item>
									<Form.Item label="位置点照片" colon={false}>
										<div>
											{item.attachments.length > 0 ?
												item.attachments.map((v, vindex) => {
													return (<span className="fl mr10"
													              key={'attc' + vindex}
													              style={{display: 'inlineBlock', textAlign: 'center'}}>
	                                           <p>{v.dateTime == 0 || v.dateTime == null ? "" : moment(v.dateTime).format("YYYY-MM-DD")}</p>
			                                      <Zmage
				                                      style={{
					                                      width: '180px',
					                                      height: '180px'
				                                      }}
				                                      src={v.filePath}
			                                      />
					                         </span>)
												}) : null}</div>
									</Form.Item>
								</div>
							)
						})
					}
					<Form.Item label="汇报内容生成形式" colon={false} className="nextStage"></Form.Item>
					<Form.Item wrapperCol={{span: 18, offset: 4}}>
						<Spin tip={this.state.tip} spinning={this.state.isLoading} delay={100}>
							<Button type="link" onClick={this.uploadImg}>点击生成并存储为PDF文档</Button>
						</Spin>
					</Form.Item>
					<Form.Item wrapperCol={{span: 18, offset: 4}}>
						<Button className="fl" type="link" onClick={this.sendReport}>点击发送邮件</Button>
						<div className="fl ml30 mr25">
							<EditableTagGroup
								data={fields.emails}
								updateState={this.updateState.bind(this, 'emails')}
							/>
						</div>
						<div className="fl">
							<PersonAddIcon
								data={fields.emailUserIds}
								person={this.state.person}
								updatePerson={this.updateState.bind(this, 'emailUserIds')}
							/></div>
					</Form.Item>
				</Form>
			</div>
		)
	}
}

const Index = Form.create({})(Demo);
export default Index;



